.report-message-title {
  padding: 20px;
  font-size: 16px;
  color: #252b3a;
  line-height: 24px;
  font-weight: bold;
}

.report-message-detail {
  font-size: 14px;
  color: #252b3a;
  line-height: 22px;
  cursor: pointer;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
